import React, { useContext, useReducer, useEffect, useState } from "react";
import cabinasReducer from "./cabinasReducer";
import cabinasContext from "./cabinasContext";
import AlertContext from "../alerts/alertContext";
import axios from "axios";

const CabinasState = (props) => {
  const initialState = {};

  const alertContext = useContext(AlertContext);
  const { setAlert } = alertContext;

  const [state, dispatch] = useReducer(cabinasReducer, initialState);

  const [mostrarModalAlta, setMostrarModalAlta] = useState(false);
  const [mostrarModalEliminar, setMostrarModalEliminar] = useState(false);
  const [mostrarModalEditar, setMostrarModalEditar] = useState(false);
  const [
    mostrarModalConfirmacionEditar,
    setMostrarModalConfirmacionEditar,
  ] = useState(false);

  const [
    mostrarModalConfirmacionAlta,
    setMostrarModalConfirmacionAlta,
  ] = useState(false);

  const [loading, setLoading] = useState(false);
  const [loadingCabinas, setLoadingCabinas] = useState(false);
  const [cabinas, setCabinas] = useState([]);

  const [datosFormulario, setDatosFormulario] = useState({
    codigo_cabina: "",
    fecha_instalacion: "",
    id_config: "",
    observaciones: "",
  });

  const formatDate = (data) => {
    var date = new Date(data);

    if (!isNaN(date.getTime())) {
      var day = date.getUTCDate().toString();
      var month = (date.getUTCMonth() + 1).toString();
      var fecha =
        (day[1] ? day : "0" + day[0]) +
        "/" +
        (month[1] ? month : "0" + month[0]) +
        "/" +
        date.getFullYear();
      return fecha;
    }
  };

  const getCabinas = async () => {
    setLoadingCabinas(true);
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    axios
      .get(process.env.REACT_APP_API_URL + "cabinas", header)
      .then((response) => {
        setCabinas(response.data.result);
        setLoadingCabinas(false);
      })
      .catch((err) => {
        if (err.response !== undefined) {
          if (err.response.status === 401 || err.response.status === 422) {
            localStorage.setItem("token", "");
            setAlert("La sesin expir", "yellow-300");
            window.location.href = "/login";
          }
        } else {
          setLoadingCabinas(false);
          setAlert("Hubo un error recuperando las cabinas", "red-300");
          console.log(err.response);
        }
      });
  };

  const altaCabina = async () => {
    setLoading(true);
    const datosFormularioModified = { ...datosFormulario };
    datosFormularioModified.fecha_instalacion = formatDate(
      datosFormulario.fecha_instalacion
    );
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    axios
      .post(
        process.env.REACT_APP_API_URL + "cabinas/new",
        datosFormularioModified,
        header
      )
      .then((response) => {
        setMostrarModalConfirmacionAlta(false);
        getCabinas();
        setLoading(false);
        setAlert("Cabina creada exitosamente", "green-300");
      })
      .catch((err) => {
        if (err.response !== undefined) {
          if (err.response.status === 401 || err.response.status === 422) {
            localStorage.setItem("token", "");
            setAlert("La sesin expir", "yellow-300");
            setLoading(false);
            window.location.href = "/login";
          }
        } else {
          setMostrarModalConfirmacionAlta(false);
          setLoading(false);
          setAlert("Hubo un error creando la cabina", "red-300");
          console.log(err.response);
        }
      });
  };

  const editarCabina = async () => {
    setLoading(true);
    const datosFormularioModified = { ...datosFormulario };
    datosFormularioModified.fecha_instalacion = formatDate(
      datosFormulario.fecha_instalacion
    );
    delete datosFormularioModified.id_config;
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    axios
      .put(
        process.env.REACT_APP_API_URL + "cabinas/" + datosFormulario.id_config,
        datosFormularioModified,
        header
      )
      .then((response) => {
        console.log(response);
        setMostrarModalConfirmacionEditar(false);
        getCabinas();
        setLoading(false);
        setAlert("Cabina editada exitosamente", "green-300");
      })
      .catch((err) => {
        if (err.response !== undefined) {
          if (err.response.status === 401 || err.response.status === 422) {
            localStorage.setItem("token", "");
            setAlert("La sesin expir", "yellow-300");
            setLoading(false);
            console.log(err.response);
            window.location.href = "/login";
          }
        } else {
          setMostrarModalConfirmacionEditar(false);
          setLoading(false);
          setAlert("Hubo un error editando la cabina", "red-300");
          console.log(err.response);
        }
      });
  };

  const bajaCabina = async () => {
    setLoading(true);
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    axios
      .delete(
        process.env.REACT_APP_API_URL + "cabinas/" + datosFormulario.id_config,
        header
      )
      .then((response) => {
        if (response.data.exito === true) {
          getCabinas();
          setMostrarModalEliminar(false);
          setDatosFormulario({});
          setAlert("Cabina eliminada exitosamente", "green-300");
          setLoading(false);
        } else {
          setMostrarModalEliminar(false);
          setDatosFormulario({});
          setAlert(response.data.message, "red-300");
          setLoading(false);
        }
      })
      .catch((err) => {
        if (err.response !== undefined) {
          if (err.response.status === 401) {
            localStorage.setItem("token", "");
            setAlert("La sesin expir", "yellow-300");
            setLoading(false);
            window.location.href = "/login";
          }
        } else {
          setMostrarModalEliminar(false);
          setDatosFormulario({});
          setAlert("Hubo un problema, intent ms tarde", "red-300");
          setLoading(false);
          console.log(err.response);
        }
      });
  };

  useEffect(() => {}, []);

  return (
    <cabinasContext.Provider
      value={{
        mostrarModalAlta,
        setMostrarModalAlta,
        mostrarModalConfirmacionAlta,
        setMostrarModalConfirmacionAlta,
        datosFormulario,
        setDatosFormulario,
        formatDate,
        altaCabina,
        editarCabina,
        bajaCabina,
        loading,
        setLoading,
        getCabinas,
        loadingCabinas,
        setLoadingCabinas,
        cabinas,
        setCabinas,
        mostrarModalEliminar,
        setMostrarModalEliminar,
        mostrarModalEditar,
        setMostrarModalEditar,
        mostrarModalConfirmacionEditar,
        setMostrarModalConfirmacionEditar,
      }}
    >
      {props.children}
    </cabinasContext.Provider>
  );
};

export default CabinasState;
