import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Login from "./components/Login.js";
import Header from "./components/Fixed/Header";
import ListadoCabinas from "./components/Cabinas/ListadoCabinas";
import ListadoPuntos from "./components/Puntos/ListadoPuntos";
import ListadoLecturas from "./components/Lecturas/ListadoLecturas";
import Footer from "./components/Fixed/Footer";
import Alert from "./components/Fixed/Alert";
import LecturasState from "./context/lecturas/lecturasState";
import CabinasState from "./context/cabinas/cabinasState";
import PuntosState from "./context/puntos/puntosState";
import AlertState from "./context/alerts/alertState";

function App() {
  return (
    <AlertState>
      <div
        className="App h-screen flex flex-col"
        style={{ backgroundColor: "whitesmoke" }}
      >
        <LecturasState>
          <CabinasState>
            <PuntosState>
              <Router>
                <Header />
                <div className="fixed right-0 top-0 mx-4 my-24">
                  <Alert />
                </div>
                <Switch>
                  <Route exact path="/">
                    <ListadoLecturas />
                  </Route>
                  <Route exact path="/cabinas">
                    <ListadoCabinas />
                  </Route>
                  <Route exact path="/puntos">
                    <ListadoPuntos />
                  </Route>
                  <Route exact path="/login">
                    <Login />
                  </Route>
                </Switch>
                <Footer />
              </Router>
            </PuntosState>
          </CabinasState>
        </LecturasState>
      </div>
    </AlertState>
  );
}

export default App;
