import React, { useMemo, useContext, useEffect, useState } from "react";
import DataTable, { defaultThemes } from "react-data-table-component";
import axios from "axios";
import ModalAltaCabina from "./ModalAltaCabina";
import ModalConfirmarAlta from "./ModalConfirmarAlta";
import ModalConfirmarEliminar from "./ModalConfirmarEliminar";
import ModalEditar from "./ModalEditarCabina";
import ModalConfirmarEditar from "./ModalConfirmarEditar";
import CabinasContext from "../../context/cabinas/cabinasContext";
import { Redirect } from "react-router";

function ListadoCabinas() {
  const cabinasContext = useContext(CabinasContext);
  const {
    mostrarModalAlta,
    setMostrarModalAlta,
    mostrarModalConfirmacionAlta,
    mostrarModalConfirmacionEditar,
    getCabinas,
    loadingCabinas,
    cabinas,
    mostrarModalEliminar,
    setMostrarModalEliminar,
    setDatosFormulario,
    mostrarModalEditar,
    setMostrarModalEditar,
  } = cabinasContext;

  useEffect(() => {
    getCabinas();
  }, []);

  useEffect(() => {}, [cabinas]);

  const customStyles = {
    headRow: {
      style: {
        borderTopStyle: "solid",
        borderTopWidth: "1px",
        borderTopColor: defaultThemes.default.divider.default,
      },
    },
    headCells: {
      style: {
        "&:not(:last-of-type)": {
          borderRightStyle: "solid",
          borderRightWidth: "1px",
          borderRightColor: defaultThemes.default.divider.default,
          fontSize: 16,
          fontWeight: 700,
        },
        borderRightStyle: "solid",
        borderRightWidth: "1px",
        borderRightColor: defaultThemes.default.divider.default,
        fontSize: 16,
        fontWeight: 700,
      },
    },
    cells: {
      style: {
        borderRightStyle: "solid",
        borderRightWidth: "1px",
        borderRightColor: defaultThemes.default.divider.default,
      },
    },
  };

  const columnas = React.useMemo(
    () => [
      {
        name: "ID Configuracin",
        selector: "id_config",
        sortable: true,
        cell: (row) => (
          <div>
            <p className="font-small" style={{ fontSize: 15 }}>
              {row.id_config}
            </p>
          </div>
        ),
      },
      {
        name: "Cdigo Cabina",
        selector: "codigo_cabina",
        sortable: true,
        cell: (row) => (
          <div>
            <p className="font-small" style={{ fontSize: 15 }}>
              {row.codigo_cabina}
            </p>
          </div>
        ),
      },
      {
        name: "Fecha Instalacin",
        selector: "fecha_instalacion",
        sortable: true,
        cell: (row) => (
          <div>
            <p className="font-small" style={{ fontSize: 15 }}>
              {row.fecha_instalacion}
            </p>
          </div>
        ),
      },
      {
        name: "Observaciones",
        selector: "observaciones",
        sortable: false,
        cell: (row) => (
          <div>
            <p className="font-small" style={{ fontSize: 15 }}>
              {row.observaciones}
            </p>
          </div>
        ),
      },
      {
        name: "Acciones",
        ignoreRowClick: true,
        right: false,
        maxWidth: "100px",
        cell: (row) => (
          <div className="flex flex-row items-center 2xl:ml-0.5">
            <button
              className="rounded p-1 font-medium bg-purple-800 text-white mr-2 "
              onClick={() => {
                var newdate = row.fecha_instalacion
                  .split("/")
                  .reverse()
                  .join("-");
                setDatosFormulario({
                  codigo_cabina: row.codigo_cabina,
                  fecha_instalacion: newdate,
                  id_config: row.id_config,
                  observaciones: row.observaciones,
                });
                setMostrarModalEditar(true);
              }}
              id={row.id_config}
            >
              <div className="flex flex-row items-center">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  class="h-5 w-5"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                >
                  <path d="M17.414 2.586a2 2 0 00-2.828 0L7 10.172V13h2.828l7.586-7.586a2 2 0 000-2.828z" />
                  <path
                    fill-rule="evenodd"
                    d="M2 6a2 2 0 012-2h4a1 1 0 010 2H4v10h10v-4a1 1 0 112 0v4a2 2 0 01-2 2H4a2 2 0 01-2-2V6z"
                    clip-rule="evenodd"
                  />
                </svg>
              </div>
            </button>
            <button
              className="rounded  p-1 font-medium bg-red-700 text-white"
              onClick={() => {
                setDatosFormulario(row);
                setMostrarModalEliminar(true);
              }}
              id={row.id_config}
            >
              <div className="flex flex-row items-center">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  class="h-5 w-5"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                >
                  <path
                    fill-rule="evenodd"
                    d="M10 18a8 8 0 100-16 8 8 0 000 16zM7 9a1 1 0 000 2h6a1 1 0 100-2H7z"
                    clip-rule="evenodd"
                  />
                </svg>
              </div>
            </button>
          </div>
        ),
      },
    ],
    []
  );

  return (
    <div className="p-4 m-4 rounded bg-blue-600 shadow-lg text-left mb-auto">
      <div className="flex flex-row justify-between">
        <div className="flex flex-row">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-10 w-10 mt-1 text-white"
            viewBox="0 0 20 20"
            fill="currentColor"
          >
            <path d="M9 2a1 1 0 000 2h2a1 1 0 100-2H9z" />
            <path
              fill-rule="evenodd"
              d="M4 5a2 2 0 012-2 3 3 0 003 3h2a3 3 0 003-3 2 2 0 012 2v11a2 2 0 01-2 2H6a2 2 0 01-2-2V5zm3 4a1 1 0 000 2h.01a1 1 0 100-2H7zm3 0a1 1 0 000 2h3a1 1 0 100-2h-3zm-3 4a1 1 0 100 2h.01a1 1 0 100-2H7zm3 0a1 1 0 100 2h3a1 1 0 100-2h-3z"
              clip-rule="evenodd"
            />
          </svg>
          <h1 className="text-white text-3xl mb-4 mt-2 font-medium">
            Listado de Cabinas
          </h1>
        </div>
        <div className="flex flex-row">
          {/*           <input
            className="rounded py-0 px-4 h-11 mr-2"
            placeholder="Buscar ID Configuracin"
          ></input> */}
          <button
            className="rounded mb-4 px-4 py-2 bg-green-500 text-white rounded-md font-medium"
            onClick={() => setMostrarModalAlta(true)}
          >
            <div className="flex flex-row items-center">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-5 w-5 "
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fill-rule="evenodd"
                  d="M10 18a8 8 0 100-16 8 8 0 000 16zm1-11a1 1 0 10-2 0v2H7a1 1 0 100 2h2v2a1 1 0 102 0v-2h2a1 1 0 100-2h-2V7z"
                  clip-rule="evenodd"
                />
              </svg>
              <p className="ml-2">Nueva Cabina</p>
            </div>
          </button>
        </div>
      </div>
      <DataTable
        className="rounded "
        title="Cabinas"
        noHeader
        striped
        progressPending={loadingCabinas}
        columns={columnas}
        data={cabinas}
        customStyles={customStyles}
      />
      {mostrarModalAlta ? <ModalAltaCabina /> : null}
      {mostrarModalConfirmacionAlta ? <ModalConfirmarAlta /> : null}
      {mostrarModalEliminar ? <ModalConfirmarEliminar /> : null}
      {mostrarModalEditar ? <ModalEditar /> : null}
      {mostrarModalConfirmacionEditar ? <ModalConfirmarEditar /> : null}
    </div>
  );
}

export default ListadoCabinas;
