import React, { useContext, useReducer, useEffect, useState } from "react";
import puntosReducer from "./puntosReducer";
import puntosContext from "./puntosContext";
import AlertContext from "../alerts/alertContext";
import axios from "axios";

const PuntosState = (props) => {
  const initialState = {};

  const alertContext = useContext(AlertContext);
  const { setAlert } = alertContext;

  const [state, dispatch] = useReducer(puntosReducer, initialState);

  const [puntos, setPuntos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [loadingPuntos, setLoadingPuntos] = useState(false);
  const [mostrarModalAlta, setMostrarModalAlta] = useState(false);
  const [mostrarModalEditar, setMostrarModalEditar] = useState(false);
  const [
    mostrarModalConfirmacionAlta,
    setMostrarModalConfirmacionAlta,
  ] = useState(false);
  const [
    mostrarModalConfirmacionEditar,
    setMostrarModalConfirmacionEditar,
  ] = useState(false);
  const [
    mostrarModalConfirmacionEliminar,
    setMostrarModalConfirmacionEliminar,
  ] = useState(false);
  const [mostrarModalInformacion, setMostrarModalInformacion] = useState(false);

  const [datosFormulario, setDatosFormulario] = useState({
    id_punto: "",
    id_tag: "",
    epc: "",
    fecha_instalacion: "",
    via: "",
    baliza: "",
    asociado_a: "",
    progresivas: "",
    latitud: "",
    longitud: "",
    observaciones: "",
  });

  const formatDate = (data) => {
    var date = new Date(data);

    if (!isNaN(date.getTime())) {
      var day = date.getUTCDate().toString();
      var month = (date.getUTCMonth() + 1).toString();
      var fecha =
        (day[1] ? day : "0" + day[0]) +
        "/" +
        (month[1] ? month : "0" + month[0]) +
        "/" +
        date.getFullYear();
      return fecha;
    }
  };

  const getPuntos = async () => {
    setLoadingPuntos(true);
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    axios
      .get(process.env.REACT_APP_API_URL + "puntos", header)
      .then((response) => {
        setPuntos(response.data.result);
        setLoadingPuntos(false);
      })
      .catch((err) => {
        if (err.response !== undefined) {
          if (err.response.status === 401 || err.response.status === 422) {
            localStorage.setItem("token", "");
            setAlert("La sesin expir", "yellow-300");
            setLoadingPuntos(false);
            window.location.href = "/login";
          }
        } else {
          setAlert("Hubo un error recuperando los puntos", "red-300");
          setLoadingPuntos(false);
          console.log(err.response);
        }
      });
  };

  const altaPunto = async () => {
    setLoading(true);
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    const datosFormularioModified = { ...datosFormulario };
    datosFormularioModified.fecha_instalacion = formatDate(
      datosFormulario.fecha_instalacion
    );
    axios
      .post(
        process.env.REACT_APP_API_URL + "puntos/new",
        datosFormularioModified,
        header
      )
      .then((response) => {
        getPuntos();
        setDatosFormulario({
          id_punto: "",
          id_tag: "",
          epc: "",
          fecha_instalacion: "",
          via: "",
          baliza: "",
          asociado_a: "",
          progresivas: "",
          latitud: "",
          longitud: "",
          observaciones: "",
        });
        setMostrarModalConfirmacionAlta(false);
        setLoading(false);
        setAlert("Punto creado exitosamente", "green-300");
      })
      .catch((err) => {
        if (err.response !== undefined) {
          if (err.response.status === 401 || err.response.status === 422) {
            localStorage.setItem("token", "");
            setAlert("La sesin expir", "yellow-300");
            setLoadingPuntos(false);
            window.location.href = "/login";
          }
        } else {
          setDatosFormulario({
            id_punto: "",
            id_tag: "",
            epc: "",
            fecha_instalacion: "",
            via: "",
            baliza: "",
            asociado_a: "",
            progresivas: "",
            latitud: "",
            longitud: "",
            observaciones: "",
          });
          setMostrarModalConfirmacionAlta(false);
          setLoading(false);
          setAlert("Hubo un error creando el punto", "red-300");
          console.log(err);
        }
      });
  };

  const editarPunto = async () => {
    setLoading(true);
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    const datosFormularioModified = { ...datosFormulario };
    datosFormularioModified.fecha_instalacion = formatDate(
      datosFormulario.fecha_instalacion
    );
    delete datosFormularioModified.id_punto;
    axios
      .put(
        process.env.REACT_APP_API_URL + "puntos/" + datosFormulario.id_punto,
        datosFormularioModified,
        header
      )
      .then((response) => {
        console.log(response);
        getPuntos();
        setDatosFormulario({
          id_punto: "",
          id_tag: "",
          epc: "",
          fecha_instalacion: "",
          via: "",
          baliza: "",
          asociado_a: "",
          progresivas: "",
          latitud: "",
          longitud: "",
          observaciones: "",
        });
        setMostrarModalConfirmacionEditar(false);
        setLoading(false);
        setAlert("Punto editado exitosamente", "green-300");
      })
      .catch((err) => {
        if (err.response !== undefined) {
          if (err.response.status === 401 || err.response.status === 422) {
            localStorage.setItem("token", "");
            setAlert("La sesin expir", "yellow-300");
            setLoadingPuntos(false);
            window.location.href = "/login";
          }
        } else {
          setDatosFormulario({
            id_punto: "",
            id_tag: "",
            epc: "",
            fecha_instalacion: "",
            via: "",
            baliza: "",
            asociado_a: "",
            progresivas: "",
            latitud: "",
            longitud: "",
            observaciones: "",
          });
          setLoading(false);
          setMostrarModalConfirmacionEditar(false);
          setAlert("Hubo un error editando el punto", "red-300");
          console.log(err);
        }
      });
  };

  const bajaPunto = async () => {
    setLoading(true);
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    axios
      .delete(
        process.env.REACT_APP_API_URL + "puntos/" + datosFormulario.id_punto,
        header
      )
      .then((response) => {
        if (response.data.exito === true) {
          getPuntos();
          setDatosFormulario({
            id_punto: "",
            id_tag: "",
            epc: "",
            fecha_instalacion: "",
            via: "",
            baliza: "",
            asociado_a: "",
            progresivas: "",
            latitud: "",
            longitud: "",
            observaciones: "",
          });
          setLoading(false);
          setMostrarModalConfirmacionEliminar(false);
          setAlert("Punto eliminado exitosamente", "green-300");
        } else {
          setDatosFormulario({
            id_punto: "",
            id_tag: "",
            epc: "",
            fecha_instalacion: "",
            via: "",
            baliza: "",
            asociado_a: "",
            progresivas: "",
            latitud: "",
            longitud: "",
            observaciones: "",
          });
          setLoading(false);
          setMostrarModalConfirmacionEliminar(false);
          setAlert(response.data.message, "red-300");
        }
      })
      .catch((err) => {
        if (err.response !== undefined) {
          if (err.response.status === 401 || err.response.status === 422) {
            localStorage.setItem("token", "");
            setAlert("La sesin expir", "yellow-300");
            setLoadingPuntos(false);
            console.log(err.response);
            window.location.href = "/login";
          }
        } else {
          setDatosFormulario({
            id_punto: "",
            id_tag: "",
            epc: "",
            fecha_instalacion: "",
            via: "",
            baliza: "",
            asociado_a: "",
            progresivas: "",
            latitud: "",
            longitud: "",
            observaciones: "",
          });
          setLoading(false);
          setMostrarModalConfirmacionEliminar(false);
          setAlert("Hubo un error eliminando el punto", "red-300");
          console.log(err);
        }
      });
  };

  useEffect(() => {}, []);

  return (
    <puntosContext.Provider
      value={{
        getPuntos,
        puntos,
        setPuntos,
        loadingPuntos,
        setLoadingPuntos,
        mostrarModalAlta,
        setMostrarModalAlta,
        mostrarModalConfirmacionAlta,
        setMostrarModalConfirmacionAlta,
        datosFormulario,
        setDatosFormulario,
        formatDate,
        loading,
        setLoading,
        altaPunto,
        mostrarModalConfirmacionEliminar,
        setMostrarModalConfirmacionEliminar,
        mostrarModalEditar,
        setMostrarModalEditar,
        mostrarModalConfirmacionEditar,
        setMostrarModalConfirmacionEditar,
        bajaPunto,
        editarPunto,
        mostrarModalInformacion,
        setMostrarModalInformacion,
      }}
    >
      {props.children}
    </puntosContext.Provider>
  );
};

export default PuntosState;
