import React, { useContext, useReducer, useEffect, useState } from "react";
import lecturasReducer from "./lecturasReducer";
import lecturasContext from "./lecturasContext";
import AlertContext from "../alerts/alertContext";
import axios from "axios";

const PuntosState = (props) => {
  const initialState = {};

  const alertContext = useContext(AlertContext);
  const { setAlert } = alertContext;

  const [state, dispatch] = useReducer(lecturasReducer, initialState);

  const [lecturas, setLecturas] = useState([]);
  const [loadingLecturas, setLoadingLecturas] = useState(false);
  const [datosInfoPunto, setDatosInfoPunto] = useState({});
  const [loading, setLoading] = useState(true);
  const [mostrarModalInformacion, setMostrarModalInformacion] = useState(false);

  const formatDate = (data) => {
    var date = new Date(data);

    if (!isNaN(date.getTime())) {
      var day = date.getUTCDate().toString();
      var month = (date.getUTCMonth() + 1).toString();
      var fecha =
        (day[1] ? day : "0" + day[0]) +
        "/" +
        (month[1] ? month : "0" + month[0]) +
        "/" +
        date.getFullYear();
      return fecha;
    }
  };

  const getLecturas = async () => {
    setLoadingLecturas(true);
    const header = {
      headers: { Authorization: "Bearer " + localStorage.getItem("token") },
    };
    const hoy = new Date();
    const hoyMenos30 = hoy.getDate() - 30;
    axios
      .get(
        process.env.REACT_APP_API_URL +
          "lecturas?desde=" +
          formatDate(hoyMenos30) +
          "&hasta=" +
          formatDate(hoy),
        header
      )
      .then((response) => {
        setLecturas(response.data.result);
        setLoadingLecturas(false);
        console.log(response);
      })
      .catch((err) => {
        if (err.response.status === 401 || err.response.status === 422) {
          localStorage.setItem("token", "");
          setAlert("La sesin expir", "yellow-300");
          setLoadingLecturas(false);
          window.location.href = "/login";
        } else {
          setLoadingLecturas(false);
          console.log(err.response);
        }
      });
  };

  useEffect(() => {}, []);

  return (
    <lecturasContext.Provider
      value={{
        getLecturas,
        lecturas,
        setLecturas,
        loadingLecturas,
        setLoadingLecturas,
        datosInfoPunto,
        setDatosInfoPunto,
        loading,
        setLoading,
        mostrarModalInformacion,
        setMostrarModalInformacion,
        formatDate,
      }}
    >
      {props.children}
    </lecturasContext.Provider>
  );
};

export default PuntosState;
