import React, { Fragment, useState, useContext, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import AlertContext from "../context/alerts/alertContext";

function Login() {
  let history = useHistory();

  /*   useEffect(() => {
    getUser();
    if (auth) {
      history.push("/");
    }
  }, [auth]); */

  const alertContext = useContext(AlertContext);
  const { setAlert } = alertContext;

  const [loginData, setLoginData] = useState({
    username: "",
    password: "",
  });
  const [textoAlerta, setTextoAlerta] = useState("");
  const [errorLogin, setErrorLogin] = useState(false);
  const [loading, setLoading] = useState(false);

  const onChange = (e) => {
    setLoginData({
      ...loginData,
      [e.target.name]: e.target.value,
    });
  };

  const onSubmit = async (e) => {
    setLoading(true);
    e.preventDefault();
    if (loginData.username.trim() === "") {
      setTextoAlerta("Por favor, ingres el nombre de usuario");
      setErrorLogin(true);
      setLoading(false);
      return;
    }
    if (loginData.password.trim() === "") {
      setTextoAlerta("Por favor, ingres la contrasea");
      setErrorLogin(true);
      setLoading(false);
      return;
    }
    await axios
      .post(process.env.REACT_APP_API_URL + "auth", {
        username: loginData.username,
        password: loginData.password,
      })
      .then((response) => {
        if (response.data.exito === false) {
          setTextoAlerta("Credenciales invlidas");
          setErrorLogin(true);
          setLoading(false);
        } else {
          localStorage.setItem("token", response.data.access_token);
          setLoading(false);
          //setAlert("Ingreso exitoso", "green-300");
          window.location.href = "/";
        }
      })
      .catch((err) => {
        console.log(err);
        setTextoAlerta("Hubo un error, intent ms tarde");
        setErrorLogin(true);
        setLoading(false);
      });
  };

  return (
    <Fragment>
      <div className="bg-blue-600 h-screen login">
        {errorLogin ? (
          <div className="flex rounded justify-between xl:mr-auto xl:ml-auto  p-4 m-4 xl:w-1/2  bg-yellow-500 items-center">
            <div className="flex flex-row">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-8 w-8 text-white mt-1 mr-3"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fill-rule="evenodd"
                  d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z"
                  clip-rule="evenodd"
                />
              </svg>
              <h1 className="font-bold text-medium text-white mt-2">
                {textoAlerta}
              </h1>
            </div>
            <button
              className="bg-yellow-500 px-1 h-6"
              onClick={() => setErrorLogin(false)}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-5 w-5 text-white"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fill-rule="evenodd"
                  d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                  clip-rule="evenodd"
                />
              </svg>
            </button>
          </div>
        ) : null}

        <div className="flex-col flex ml-auto mr-auto items-center pt-10 xl:pt-40 w-full lg:w-2/4 md:w-3/5">
          <div className="flex justify-center mb-10 px-6">
            <h1 className="text-white text-3xl">
              Gestin Trenes Linea Sarmiento
            </h1>
          </div>
          <form
            onSubmit={onSubmit}
            className="mt-2 flex flex-col lg:w-1/2 w-full px-6"
          >
            <label
              className="block uppercase tracking-wide text-white text-left text-xs font-bold mb-2"
              htmlFor="username"
            >
              Usuario
            </label>
            <div className="flex flex-wrap items-stretch w-full mb-4 relative h-15 bg-white items-center rounded mb-6 shadow-lg">
              <div className="flex -mr-px justify-center w-15 px-1 bg-gray-300 rounded-l">
                <span className="flex items-center leading-normal px-2 border-0 text-2xl">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    class="h-5 w-5"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                      clip-rule="evenodd"
                    />
                  </svg>
                </span>
              </div>
              <input
                type="text"
                name="username"
                id="username"
                className="flex-shrink flex-grow flex-auto leading-normal flex-1 border-0 h-12 px-3 self-center rounded rounded-l-none relative outline-none"
                placeholder="Usuario"
                onChange={onChange}
                value={loginData.username}
              />
            </div>
            <label
              className="block uppercase tracking-wide text-white text-xs font-bold mb-2 text-left"
              htmlFor="password"
            >
              Contrasea
            </label>
            <div className="flex flex-wrap items-stretch w-full mb-4 relative h-15 bg-white items-center rounded mb-6 shadow-lg">
              <div className="flex -mr-px justify-center w-15 px-1 bg-gray-300 rounded-l">
                <span className="flex items-center leading-normal bg-gray-300 px-2 border-0 text-2xl">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                    className="h-5 w-5 text-mainBlue"
                  >
                    <path
                      fillRule="evenodd"
                      d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z"
                      clipRule="evenodd"
                    />
                  </svg>
                </span>
              </div>
              <input
                type="password"
                name="password"
                id="password"
                className="flex-shrink flex-grow flex-auto leading-normal flex-1 border-0 rounded rounded-l-none h-12 px-3 self-center relative outline-none"
                placeholder="***********"
                onChange={onChange}
                value={loginData.password}
              />
            </div>

            <button
              type="submit"
              className="bg-blue-500 flex items-center justify-center shadow-lg uppercase font-medium py-4 text-center px-17 md:px-12 md:py-4 text-white rounded leading-tight text-lg mt-4 mb-20 transition duration-500 ease-in-out "
            >
              Ingresar
              {loading ? (
                <svg
                  className="animate-spin ml-3 h-5 w-5 text-white"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                >
                  <circle
                    className="opacity-25"
                    cx="12"
                    cy="12"
                    r="10"
                    stroke="currentColor"
                    strokeWidth="4"
                  ></circle>
                  <path
                    className="opacity-75"
                    fill="currentColor"
                    d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                  ></path>
                </svg>
              ) : null}
            </button>
          </form>
        </div>
      </div>
    </Fragment>
  );
}

export default Login;
