import React, { useContext, useState, useEffect } from "react";

import PuntosContext from "../../context/puntos/puntosContext";

function ModalEditarPunto() {
  const puntosContext = useContext(PuntosContext);
  const {
    setMostrarModalEditar,
    setMostrarModalConfirmacionEditar,
    datosFormulario,
    setDatosFormulario,
  } = puntosContext;

  const [errorFormulario, setErrorFormulario] = useState(false);

  const updateValue = (e) => {
    var newDatos = { ...datosFormulario };
    newDatos[e.target.name] = e.target.value;
    setDatosFormulario(newDatos);
  };

  const validateFormulario = () => {
    for (const [key, value] of Object.entries(datosFormulario)) {
      if (key !== "observaciones" && value === "") {
        setErrorFormulario(true);
        return false;
      }
    }
    setMostrarModalEditar(false);
    setMostrarModalConfirmacionEditar(true);
    return true;
  };

  return (
    <div className="fixed z-10 inset-0 overflow-y-auto">
      <div className="flex items-end justify-center min-h-screen pt-4 px-6 pb-20 text-center sm:block sm:p-0">
        <div className="fixed inset-0 transition-opacity" aria-hidden="true">
          <div className="absolute inset-0 bg-gray-500 opacity-75"></div>
        </div>
        <span
          className="hidden sm:inline-block sm:align-middle sm:h-screen"
          aria-hidden="true"
        >
          &#8203;
        </span>

        <div
          className="inline-block rounded align-bottom bg-blue-600 text-left overflow-hidden shadow-xl lg:align-middle transform transition-all w-6/12 2xl:w-2/5"
          role="dialog"
          aria-modal="true"
          aria-labelledby="modal-headline"
        >
          <div className="flex justify-between bg-blue-800 border-b-2 p-4 pb-0 ">
            <div>
              <h1 className="font-bold text-2xl text-white">Punto</h1>
              <h2 className="font-medium text-white mb-3">
                Formulario de edicin
              </h2>
            </div>
            <button
              className=" px-1 h-8 mt-3"
              onClick={() => {
                setDatosFormulario({
                  id_punto: "",
                  id_tag: "",
                  epc: "",
                  fecha_instalacion: "",
                  via: "",
                  baliza: "",
                  asociado_a: "",
                  progresivas: "",
                  latitud: "",
                  longitud: "",
                  observaciones: "",
                });
                setMostrarModalEditar(false);
              }}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-6 w-6 text-white"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fill-rule="evenodd"
                  d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                  clip-rule="evenodd"
                />
              </svg>
            </button>
          </div>
          <div>
            {errorFormulario ? (
              <div className="flex justify-between p-4 ml-6 mr-6 mt-4 rounded bg-red-400 items-center">
                <div className="flex flex-row">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-8 w-8 text-white mt-1 mr-3"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z"
                      clip-rule="evenodd"
                    />
                  </svg>
                  <h1 className="font-bold text-medium text-white mt-2">
                    Por favor, complet todos los campos requeridos
                  </h1>
                </div>
                <button
                  className="bg-red-400 px-1 h-6 "
                  onClick={() => {
                    setErrorFormulario(false);
                  }}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    class="h-5 w-5 text-white"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                      clip-rule="evenodd"
                    />
                  </svg>
                </button>
              </div>
            ) : null}
            <div
              className="grid grid-cols-1 xl:grid-cols-2 gap-1 p-4
            "
            >
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">ID PUNTO *</label>
                <input
                  id="id_punto"
                  name="id_punto"
                  className="rounded mt-1 block w-full bg-gray-300  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="ID Punto *"
                  type="text"
                  value={datosFormulario.id_punto}
                  onChange={updateValue}
                  disabled
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">ID TAG *</label>
                <input
                  id="id_tag"
                  name="id_tag"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="ID Tag *"
                  type="text"
                  value={datosFormulario.id_tag}
                  onChange={updateValue}
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">
                  FECHA DE INSTALACIN *
                </label>
                <input
                  id="fecha_instalacion"
                  name="fecha_instalacion"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  type="date"
                  value={datosFormulario.fecha_instalacion}
                  onChange={updateValue}
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">EPC</label>
                <input
                  id="epc"
                  name="epc"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="EPC *"
                  type="text"
                  value={datosFormulario.epc}
                  onChange={updateValue}
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">VA *</label>
                <input
                  id="via"
                  name="via"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="Va *"
                  type="text"
                  value={datosFormulario.via}
                  onChange={updateValue}
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">BALIZA *</label>
                <input
                  id="baliza"
                  name="baliza"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="Baliza *"
                  type="text"
                  value={datosFormulario.baliza}
                  onChange={updateValue}
                ></input>
              </div>

              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">LATITUD *</label>
                <input
                  id="latitud"
                  name="latitud"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="Latitud *"
                  type="text"
                  value={datosFormulario.latitud}
                  onChange={updateValue}
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">LONGITUD *</label>
                <input
                  id="longitud"
                  name="longitud"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="Longitud *"
                  type="text"
                  value={datosFormulario.longitud}
                  onChange={updateValue}
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">ASOCIADO A *</label>
                <input
                  id="asociado_a"
                  name="asociado_a"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="Asociado a *"
                  type="text"
                  value={datosFormulario.asociado_a}
                  onChange={updateValue}
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0">
                <label className=" font-medium text-white">PROGRESIVAS *</label>
                <input
                  id="progresivas"
                  name="progresivas"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  placeholder="Progresivas *"
                  type="text"
                  value={datosFormulario.progresivas}
                  onChange={updateValue}
                ></input>
              </div>
              <div className="p-2 pb-0 mb-0 col-span-2">
                <label className=" font-medium text-white">OBSERVACIONES</label>
                <input
                  id="observaciones"
                  name="observaciones"
                  className="rounded mt-1 block w-full  h-10 bg-white p-2 shadow mb-4 transition ease-in-out duration-300 focus:outline-none focus:ring-2 focus:ring-green-400 focus:border-transparent"
                  type="text"
                  placeholder="Observaciones"
                  value={datosFormulario.observaciones}
                  onChange={updateValue}
                ></input>
              </div>
            </div>
            <div className="bg-blue-600 flex justify-end p-2 pb-4 mr-4">
              <button
                className="bg-green-500 text-white px-10 py-2 rounded font-medium"
                onClick={() => validateFormulario()}
              >
                Validar
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ModalEditarPunto;
